import React, { useState } from 'react';
import styled from 'styled-components';
import TemperatureSlider from '../../../components/temperatureSlider/temperatureSlider';
import WaterSlider from '../../../components/waterSlider/waterSlider';
import VoiceControlBtn from '../../../components/voiceControlBtn/voiceControlBtn';
import Guide from './guide';

const TemperaturePageWrapper = styled.div`
  background-color: white;
  height: ${props => props.theme.content.height};
  min-width: ${props => props.theme.app.minWidth};
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  position: relative;
`;

function TemperaturePage({ easy }: { easy: boolean }) {
  const [temperature, setTemperature] = useState<number>(30);

  return (
    <TemperaturePageWrapper>
      <Guide />
      <TemperatureSlider
        easy={easy}
        temperature={temperature}
        setTemperature={setTemperature}
      />
      <WaterSlider easy={easy} />
      <VoiceControlBtn
        easy={easy}
        temperature={temperature}
        setTemperature={setTemperature}
      />
    </TemperaturePageWrapper>
  );
}

export default TemperaturePage;
