import React from 'react';
import styled from 'styled-components';
import { Layout } from 'antd';
import { BasicProps } from 'antd/lib/layout/layout';
import { useTimer } from '../../hooks/index';
import { formatDate } from '../../utils/index';

const Header = styled(Layout.Header)`
  position: relative;
  color: white;
  width: 100%;
  margin: auto auto;
  box-shadow: 0 0 10px #000000;
  font-weight: 800;
  background-color: ${props => props.theme.color.main};
  display: flex;
  height: ${props => props.theme.header.height};
  transition: all 0.3s ease-in-out;
  .app-clock {
    font-size: ${props => props.theme.font.sm};
    margin: auto;
  }
  .app-title {
    margin: auto;
    position: relative;
    flex-grow: 1;
    color: white;
    text-align: center;
    font-size: ${props => props.theme.font.sm};
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: bold;
    letter-spacing: 0.0075rem;
    /* right: -100px; */
  }
  .app-mode-btn {
    position: absolute;
    background: rgba(255,255,255,0.4);
    border: none;
    font-size: 1.4rem;
    right: 230px;
    width: 100px;
    font-weight: bold;
    height: 100%;
    &:focus {
      outline: 0 !important;
    }
  }
`;

interface Props {
  setMode: React.Dispatch<React.SetStateAction<boolean>>;
  easy: boolean;
}

function AppHeader({ setMode, easy, ...props }: BasicProps & Props) {
  const date = new Date();
  const timer = useTimer(date);

  return (
    <Header {...props}>
      <p className="app-clock">{formatDate(date)}</p>
      <h6 className="app-title"> Smart Shower </h6>
      <p className="app-clock">{timer.toLocaleTimeString()}</p>
      <button
        type="button"
        className="app-mode-btn"
        onClick={() => setMode(!easy)}
      >
        {easy ? 'Normal' : 'Easy'}
      </button>
    </Header>
  );
}

export default AppHeader;
