/* eslint-disable no-underscore-dangle, @typescript-eslint/indent */
import { observable, action } from 'mobx';
import { createContext } from 'react';
import { message } from 'antd';
import { API } from '../assets/const';
import AuthService from '../services/Authentication';
// import ClientService from '../services/Client';
import ChildService from '../services/Child';
import DemoProfileStore from './profileStore.demo';


class ProfileStore {
  /*
   * Profile Related Attributes and Functions
   */
  @observable
  clientProfile: ClientProfile | null = null;

  @observable
  selectedChildProfile: ChildProfile | undefined | null
    = this.clientProfile?.children[0];

  @observable
  isLogin: boolean = false;

  @observable
  isLoading: boolean = false;

  @observable
  isAudioLoading: boolean = false;

  @observable
  isVideoLoading: boolean = false;

  static async getChildrenData(clientId: number):
    Promise<undefined | ChildProfile[]> {
    const [err, childrenData] = await ChildService.getByClient(clientId);
    if (!childrenData || err) {
      message.error({
        content: err ? err.error : 'Unexpected Error',
        style: {
          color: 'black',
        },
      });
      return undefined;
    }

    return childrenData;
  }

  @action.bound
  async checkSession() {
    const [err, clientProfile] = await AuthService.checkSession();
    if (!clientProfile || err) {
      message.error({
        content: err ? err.error : 'Not Authorized',
        style: {
          color: 'black',
        },
      });
      this.logout();
      return false;
    }

    this.currentAudio = clientProfile.audioCollection;
    this.currentVideo = clientProfile.video;

    const childrenData
      = await ProfileStore.getChildrenData(clientProfile._id);
    if (!childrenData || childrenData.length < 1) return true;

    clientProfile.children = childrenData;
    this.setSelectedChildProfile(childrenData[0]);
    this.clientProfile = clientProfile;

    this.isLogin = true;
    return true;
  }

  @action.bound
  async login(credentials: { client: string, password: string }) {
    const [err, clientData] = await AuthService.login(credentials);
    if (!clientData || err) {
      message.error({
        content: err ? err.error : 'Not Authorized',
        style: {
          color: 'black',
        },
      });
      return false;
    }

    const childrenData
      = await ProfileStore.getChildrenData(clientData.client._id);
    if (!childrenData || childrenData.length < 1) return true;

    clientData.client.children = childrenData;
    this.setSelectedChildProfile(childrenData[0]);
    this.clientProfile = clientData?.client ? clientData.client : null;

    this.isLogin = true;
    return true;
  }

  @action.bound
  logout() {
    AuthService.logout();
    this.clientProfile = null;
    this.isLogin = false;
  }

  @action.bound
  updateClient(client: string) {
    (this.clientProfile as ClientProfile).client = client;
  }

  @action.bound
  async addChildProfile(childProfile: ChildProfile) {
    const [err, addedChildProfile] = await ChildService.add(childProfile);
    if (!addedChildProfile || err) {
      if (err?.code === 401) this.logout();
      message.error({
        content: err ? err.error : 'Unexpected Error',
        style: {
          color: 'black',
        },
      });
    } else {
      this.setSelectedChildProfile(addedChildProfile);
      (this.clientProfile as ClientProfile).children.unshift(addedChildProfile);

      message.success({
        content: 'Profile has been added',
        style: {
          color: 'red',
        },
      });
    }
  }

  @action.bound
  async updateChildProfile(childProfile: ChildProfile) {
    const [err, updatedChildProfile] = await ChildService.update(childProfile);
    if (!updatedChildProfile || err) {
      if (err?.code === 401) this.logout();
      message.error({
        content: err ? err.error : 'Unexpected Error',
        style: {
          color: 'black',
        },
      });
    } else {
      const targetProfile = (this.clientProfile as ClientProfile).children.find(
        child => child._id === childProfile._id
      );

      if (targetProfile) {
        targetProfile.name = childProfile.name;
        targetProfile.dob = childProfile.dob;
        targetProfile.iconId = childProfile.iconId;
        targetProfile.fColor = childProfile.fColor;
        targetProfile.fVideo = childProfile.fVideo;
        targetProfile.fCartoon = childProfile.fCartoon;
        targetProfile.fTemperature = childProfile.fTemperature;
        targetProfile.temperatureRange = childProfile.temperatureRange;
        targetProfile.timer = childProfile.timer;
        targetProfile.medicalNotes = childProfile.medicalNotes;
      } else {
        message.error({
          content: 'Profile not exist',
          style: {
            color: 'black',
          },
        });
      }

      message.success({
        content: 'Profile has been updated',
        style: {
          color: 'black',
        },
      });
    }
  }

  @action.bound
  async removeChildProfile(id: number) {
    const [err, deletedChildProfile] = await ChildService.delete(id);
    if (!deletedChildProfile || err) {
      if (err?.code === 401) this.logout();
      message.error({
        content: err ? err.error : 'Unexpected Error',
        style: {
          color: 'black',
        },
      });
    } else {
      (this.clientProfile as ClientProfile).children
        = (this.clientProfile as ClientProfile).children.filter(
          child => child._id !== id
        );
      this.setSelectedChildProfile(this.clientProfile?.children[0]);

      message.success({
        content: 'Profile has been removed',
        style: {
          color: 'black',
        },
      });
    }
  }

  @action.bound
  setSelectedChildProfile(childProfile: ChildProfile | null | undefined) {
    this.selectedChildProfile = childProfile;
  }

  /*
   * Audio/Video Related Attributes and Functions
   */
  @observable
  currentAudio: { id: number, cover: string } | undefined
    = this.clientProfile?.audioCollection;

  @observable
  currentVideo: { id: string } | undefined
    = this.clientProfile?.video;

  @observable
  audioCollections: AudioCollection[] = [];

  @observable
  videoResults: VideoResult[] = [];

  @action.bound
  async searchAudioCollection(name: string) {
    if (!name) return;
    this.isAudioLoading = true;

    const res = await fetch('/search/playlists_without_albums?' +
      `q=${name}` +
      `&client_id=${API.SOUND_CLOUD_CLIENT_ID}` +
      '&limit=20' +
      '&offset=0' +
      '&linked_partitioning=1' +
      `&app_version=${API.SOUND_CLOUD_APP_VERSION}` +
      '&app_locale=en');
    const data = await res.json();

    this.isAudioLoading = false;
    this.audioCollections = (data as AudioResponse).collection;
    if (this.videoResults === undefined || this.audioCollections.length === 0) {
      message.error({
        content: 'Track Not Found',
        style: {
          color: 'black',
        },
      });
    }
  }

  @action.bound
  async searchVideo(name: string) {
    if (!name) return;
    this.isVideoLoading = true;

    const res = await fetch(`${API.YOUTUBE_BASE_API}/search?` +
      `q=${name}` +
      `&key=${API.YOUTUBE_API_KEY}` +
      '&maxResults=20' +
      '&part=snippet' +
      '&type=video'
    );
    const data = await res.json();

    this.isVideoLoading = false;
    this.videoResults = (data as VideoResponse).items;
    if (this.videoResults === undefined || this.videoResults.length === 0) {
      message.error({
        content: 'Video Not Found',
        style: {
          color: 'black',
        },
      });
    }
  }

  @action.bound
  setVideo(id: string) {
    this.currentVideo = { id };
    (this.clientProfile as ClientProfile).video = { id };
    this.clearVideoResults();
  }

  @action.bound
  clearVideoResults() {
    this.videoResults = [];
  }

  @action.bound
  setAudioCollection(id: number, cover: string) {
    this.currentAudio = { id, cover };
    (this.clientProfile as ClientProfile).audioCollection
      = { id, cover };
    this.clearAudioCollection();
  }

  @action.bound
  clearAudioCollection() {
    this.audioCollections = [];
  }
}

export const ProfileStoreCtx =
  process.env.NODE_ENV === 'test'
    ? createContext(new ProfileStore())
    : createContext(new DemoProfileStore() as unknown as ProfileStore);

