/* eslint-disable no-underscore-dangle */
import http from './http';

export default class ChildService {
  static api = 'children';

  static async getByClient(id: number):
  Promise<[undefined, ChildProfile[]] | [MyError, undefined]> {
    const request = {
      url: `${ChildService.api  }/client/${  id}`,
      requireAuth: true
    };
    try {
      const res = await http(request);

      const data: ChildProfile[] = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }

  static async add(child: ChildProfile):
  Promise<[undefined, ChildProfile] | [MyError, undefined]> {
    const request = {
      url: `${ChildService.api  }/`,
      options: {
        method: 'POST',
        body: JSON.stringify(child),
      },
      requireAuth: true
    };
    try {
      const res = await http(request);

      const data: ChildProfile = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }

  static async update(child: ChildProfile):
  Promise<[undefined, ChildProfile] | [MyError, undefined]> {
    const request = {
      url: `${ChildService.api  }/${  child._id}`,
      options: {
        method: 'PUT',
        body: JSON.stringify(child),
      },
      requireAuth: true
    };
    try {
      const res = await http(request);

      const data: ChildProfile = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }

  static async delete(id: number):
  Promise<[undefined, ChildProfile] | [MyError, undefined]> {
    const request = {
      url: `${ChildService.api  }/${  id}`,
      options: {
        method: 'DELETE',
      },
      requireAuth: true
    };
    try {
      const res = await http(request);

      const data: ChildProfile = await res.json();
      return [undefined, data];
    } catch (err) {
      return [err, undefined];
    }
  }
}
