import React, { useState } from 'react';
import styled from 'styled-components';
import { Slider } from 'antd';
import { ClockCircleOutlined } from '@ant-design/icons';
import Timer from 'react-compound-timer';

type TimerStateValues = 'INITED' | 'PLAYING' | 'PAUSED' | 'STOPPED';

const Wrapper = styled.div`
  display: flex;
  position: relative;
  height: 140px;
  flex-direction: column;

  .water-display {
    span {
      font-size: ${props => props.theme.font.md};
      vertical-align: baseline;
      color: gray;
    }
    position: relative;
    width: 100%;
    margin: auto;
    font-size: ${props => props.theme.font.lg};
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 400;
    line-height: 1.167;
    letter-spacing: 0em;
    color: black;
  }
`;

const InnerWrapper = styled.div`
  display: flex;
  padding: 5px;
  font-size: 20px;
  position: relative;
  height: 80px;

  .water-icon {
    fill: currentColor;
    width: 1em;
    height: 1em;
    display: inline-block;
    font-size: 1.5rem;
    transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    flex-shrink: 0;
    user-select: none;
    margin-left: 5px;
    margin-right: 5px;
  }

  /* style for hover */
  .ant-slider:hover {
    .ant-slider-track {
      background: #3f51b5;
      opacity: 1;
    }

    .ant-slider-rail {
      background: #3f51b5;
      opacity: 0.38;
    }

    .ant-slider-handle {
      opacity: 1;
      box-shadow: 0px 0px 0px 8px rgba(63, 81, 181, 0.16);
    }
  }

  /* style for active */
  .ant-slider:active {
    .ant-slider-handle {
      box-shadow: 0px 0px 0px 14px rgba(63, 81, 181, 0.16);
    }
  }

  /* style for not hover */
  .ant-slider {
    width: ${props => `calc(${props.theme.app.minWidth}/1.7)`};
    .ant-slider-rail {
      transition: opacity 0.3s;
      opacity: 0.28;
      border-radius: 10px;
      height: 3px;
      background: #3f51b5;
    }

    .ant-slider-step {
      display: none;
    }

    .ant-slider-track {
      transition: opacity 0.3s;
      opacity: 0.8;
      border-radius: 10px;
      height: 3px;
      background: #3f51b5;
    }

    .ant-slider-handle {
      transition: background-color 0.3s;
      border: solid 0px;
      background-color: #3f51b5;
      transition: box-shadow 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
      opacity: 1;
    }

    .ant-slider-mark {
      top: 24px;
    }
  }

`;

const TimerWrapper = styled.div`
  position: absolute;
  top: 40px;
  left: 0; 
  right: 0; 
  span {
    font-size: ${props => props.theme.font.sm}
  }
`;

function WaterSlider({ easy }: { easy: boolean }) {
  const [water, setWater] = useState(0);
  const [finished, setFinished] = useState(false);
  const countdown = 30 * 1000;

  const onFinish = () => {
    setWater(0);
    setFinished(true);
  };

  const onChange = (
    value: number,
    start: () => void,
    resume: () => void,
    pause: () => void,
    getTimerState: () => TimerStateValues
  ): void => {
    setWater(value);
    if (value > 0 && getTimerState() === 'INITED') {
      start();
    } else if (value > 0 && getTimerState() === 'PAUSED') {
      resume();
    } else if (value === 0 && getTimerState() === 'PLAYING') {
      pause();
    }
  };

  const toolTipFormatter = (value?: number | undefined) => {
    return `${value}%`;
  };

  return (
    <Wrapper className='water-slider-wapper'>
      <h1 className='water-display'>
        <span>{easy ? 'Volume ' : ''}</span>{water}%
      </h1>
      <InnerWrapper>
        <Timer
          initialTime={countdown}
          direction="backward"
          startImmediately={false}
          formatValue={(value) => `${(value < 10 ? `0${value}` : value)}`}
          checkpoints={[
            {
              time: 0,
              callback: onFinish,
            }
          ]}
        >
          {({ start, resume, pause, getTimerState }: {
            start: () => void;
            resume: () => void;
            pause: () => void;
            getTimerState: () => TimerStateValues;
          }) =>
            (
              <>
                <svg className="water-icon" focusable="false" viewBox="0 0 24 24" aria-hidden="true" role="presentation">
                  <path d="M20.65 20.87l-2.35-2.35-6.3-6.29-3.56-3.57-1.42-1.41L4.27 4.5 3 5.77l2.78 2.78c-2.55 3.14-2.36 7.76.56 10.69C7.9 20.8 9.95 21.58 12 21.58c1.79 0 3.57-.59 5.03-1.78l2.7 2.7L21 21.23l-.35-.36zM12 19.59c-1.6 0-3.11-.62-4.24-1.76C6.62 16.69 6 15.19 6 13.59c0-1.32.43-2.57 1.21-3.6L12 14.77v4.82zM12 5.1v4.58l7.25 7.26c1.37-2.96.84-6.57-1.6-9.01L12 2.27l-3.7 3.7 1.41 1.41L12 5.1z" />
                </svg>
                <Slider
                  value={water}
                  min={0}
                  max={100}
                  onChange={(value: number) => {
                    onChange(value, start, resume, pause, getTimerState);
                  }}
                  tipFormatter={toolTipFormatter}
                  disabled={finished}
                />
                <svg className="water-icon" focusable="false" viewBox="0 0 24 24" aria-hidden="true" role="presentation">
                  <path d="M17.66 7.93L12 2.27 6.34 7.93c-3.12 3.12-3.12 8.19 0 11.31C7.9 20.8 9.95 21.58 12 21.58c2.05 0 4.1-.78 5.66-2.34 3.12-3.12 3.12-8.19 0-11.31zM12 19.59c-1.6 0-3.11-.62-4.24-1.76C6.62 16.69 6 15.19 6 13.59s.62-3.11 1.76-4.24L12 5.1v14.49z" />
                </svg>
                <TimerWrapper>
                  <span>
                    <ClockCircleOutlined /> <Timer.Minutes />:<Timer.Seconds />
                  </span>
                </TimerWrapper>
              </>
            )}
        </Timer>
      </InnerWrapper>
    </Wrapper>

  );
}

export default WaterSlider;
